import React, { useState } from 'react';
import gql from 'graphql-tag';
import { useQuery } from '@apollo/react-hooks';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

import Meta from './Meta';
import Button from './Button';

const StyledCountry = styled.div`
  color: rgb(${p => p.theme.white});
  overflow-x: hidden;

  .image-container {
    position: relative;
    max-width: 1000px;
    margin: 0 auto;
    margin-bottom: 1rem;

    img {
      width: 100%;
    }

    .name {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      margin: 0;
      background: rgba(${p => p.theme.dark}, 0.9);
      text-align: center;
      width: 90vw;
      max-width: 300px;
      padding: 0.5rem;
    }
  }

  a {
    position: absolute;
    top: 0;
    left: 0;
  }

  table {
    border: 1px solid rgb(${p => p.theme.gray});
    border-collapse: collapse;
    width: 90%;
    max-width: 1000px;
    margin: 0 auto;

    border-radius: 4px;
    tbody {
    }

    tr {
      :hover {
        background-color: rgb(${p => p.theme.gray});
      }

      td {
        padding: 0.5rem;
        border-bottom: 1px solid rgb(${p => p.theme.gray});

        :first-child {
          padding-right: 1rem;
        }
      }
    }
  }
`;

const COUNTRY_QUERY = gql`
  query COUNTRY_QUERY($name: String!) {
    country(name: $name) {
      name
      nativeName
      flag
      capital
      region
      subregion
      population
      gini
      latlng
      timezones
      currencies {
        code
        name
        symbol
      }
      languages {
        iso639_1
        iso639_2
        name
        nativeName
      }
    }
  }
`;

const Country = props => {
  const [imageLoaded, setImageLoaded] = useState(false);

  const { loading, error, data } = useQuery(COUNTRY_QUERY, {
    variables: { name: props.match.params.name },
  });

  if (loading)
    return (
      <StyledCountry>
        <h2>Loading...</h2>
      </StyledCountry>
    );

  if (error) {
    console.log(error);
    return null;
  }

  const {
    name,
    nativeName,
    flag,
    capital,
    region,
    subregion,
    population,
    gini,
    latlng,
    timezones,
    currencies,
    languages,
  } = data.country;

  return (
    <StyledCountry img={flag}>
      <Meta title={name} />
      <div className="image-container">
        <img
          src={flag}
          alt={`${name} flag`}
          onLoad={() => setImageLoaded(true)}
        />
        {imageLoaded && (
          <div className="name">
            {name === nativeName ? (
              <h1>{name}</h1>
            ) : (
              <h1>
                {name}
                <br />({nativeName})
              </h1>
            )}
          </div>
        )}
      </div>
      <Link to="/">
        <Button>Home</Button>
      </Link>
      <table>
        <tbody>
          <tr>
            <td>Capitol</td>
            <td>{capital}</td>
          </tr>
          <tr>
            <td>Population</td>
            <td>{population.toLocaleString()}</td>
          </tr>
          <tr>
            <td>Region</td>
            <td>{region}</td>
          </tr>
          <tr>
            <td>Subregion</td>
            <td>{subregion}</td>
          </tr>
          <tr>
            <td>Latitude</td>
            <td>{latlng[0].toFixed(1)}</td>
          </tr>
          <tr>
            <td>Longitude</td>
            <td>{latlng[1].toFixed(1)}</td>
          </tr>
          <tr>
            <td>Gini</td>
            <td>{gini && gini.toFixed(1)}</td>
          </tr>
          <tr>
            <td>Languages</td>
            <td>{languages.map(lang => lang.name).join(', ')}</td>
          </tr>
          <tr>
            <td>Currencies</td>
            <td>
              {currencies
                .map(
                  cur =>
                    `${cur.name} (${cur.symbol ? cur.symbol + ' ' : ''}${
                      cur.code
                    })`
                )
                .join(', ')}
            </td>
          </tr>
          <tr>
            <td>Timezones</td>
            <td>{timezones.join(', ')}</td>
          </tr>
        </tbody>
      </table>
      <br />
    </StyledCountry>
  );
};

export default Country;
