import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';

import { ReactComponent as MapSVG } from '../assets/worldmap.svg';
import Meta from './Meta';

const StyledMapSVG = styled(MapSVG)`
  width: 100%;
  fill: rgb(${p => p.theme.white});
  stroke: rgb(${p => p.theme.black});
  opacity: 0;
  transition: opacity 0.5s;

  path {
    cursor: pointer;

    :hover {
      fill: rgb(${p => p.theme.darkwhite});
    }
  }
`;

const Map = props => {
  const svgRef = useRef();
  const totalAnimationTime = 2000;

  // Tell App.js which country is currently being hovered
  // Doing this the ugly old way until I learn more about external SVG
  useEffect(() => {
    svgRef.current.style.opacity = 1;
    const countries = document.querySelectorAll('path');

    // Add event listeners and incrementally show countries
    countries.forEach((country, i) => {
      country.addEventListener('mouseenter', mouseEnterHandler);
      country.addEventListener('mouseleave', mouseLeaveHandler);
    });

    return () => {
      countries.forEach(country => {
        country.removeEventListener('mouseenter', mouseEnterHandler);
        country.removeEventListener('mouseleave', mouseLeaveHandler);
      });
    };
    // eslint-disable-next-line
  }, []);

  const mouseEnterHandler = e => {
    props.setCurrentCountry([e.target.id, e.target.dataset.name]);
  };
  const mouseLeaveHandler = () => {
    props.setCurrentCountry([]);
  };

  const clickHandler = e => {
    if (e.target.id === '') return;

    props.setCurrentCountry([]); // Hide HoveringLabel
    props.history.history.push(`/country/${e.target.id.toLowerCase()}`);
  };

  return (
    <>
      <Meta title="Countries" />
      <StyledMapSVG
        onClick={clickHandler}
        onMouseMove={props.onMouseMove}
        ref={svgRef}
        animation={totalAnimationTime}
      />
    </>
  );
};

export default Map;
