import styled from 'styled-components';

const Button = styled.button`
  color: rgb(${p => p.theme.white});
  text-decoration: none;
  border: none;
  padding: 0.5rem;
  margin: 0.5rem;
  background-color: rgb(${p => p.theme.gray});
  cursor: pointer;
  border-radius: ${p => p.theme.br};
  box-shadow: ${p => p.theme.bs};

  :hover,
  :focus {
    background-color: rgb(${p => p.theme.dark});
  }
`;

export default Button;
