import React from 'react';
import styled from 'styled-components';

const StyledLabel = styled.div`
  position: absolute;
  pointer-events: none;
  transform: ${p =>
    p.flip ? 'translate(calc(-100% - 1rem), -50%)' : 'translate(1rem, -50%)'};
  padding: 0.25rem 0.5rem;
  background: rgb(${p => p.theme.gray});
  color: rgb(${p => p.theme.white});
  border-radius: ${p => p.theme.br};
  box-shadow: ${p => p.theme.bs};
  opacity: ${p => (p.name ? 1 : 0)};
  text-transform: capitalize;
`;

const HoveringLabel = ({ name, mouseX, mouseY }) => (
  <StyledLabel
    name={name}
    style={{
      left: `${mouseX}px`,
      top: `${mouseY}px`,
    }}
    x={mouseX}
    flip={mouseX > document.body.clientWidth - 200}
  >
    {name}
  </StyledLabel>
);

export default HoveringLabel;
