import React, { useState } from 'react';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
import styled, { ThemeProvider } from 'styled-components';

import Map from './components/Map';
import Country from './components/Country';
import About from './components/About';
import HoveringLabel from './components/HoveringLabel';
import Button from './components/Button';

const theme = {
  black: '14, 14, 31',
  dark: '34, 34, 51',
  white: '240, 240, 245',
  darkwhite: '221, 221, 238',
  gray: '58, 58, 92',
  br: '4px',
  tablet: 600,
  laptop: 800,
  desktop: 1200,
};
// Themes that use other themes
theme.bs = `
  0 2px 4px rgba(${theme.black}, 0.5),
  0 4px 10px rgba(${theme.black}, 0.4)  
`;

const StyledApp = styled.div`
  color: rgb(${theme.white});

  .btn {
    position: absolute;
    top: 0;
    left: 0;
  }
`;

const App = () => {
  const [currentCountry, setCurrentCountry] = useState([]);
  const [mousePosition, setMousePosition] = useState([0, 0]);
  const showHoverLabel = document.body.clientWidth >= theme.tablet;

  const setMousePositionHandler = ({ pageX, pageY }) => {
    setMousePosition([pageX, pageY]);
  };

  return (
    <ThemeProvider theme={theme}>
      <StyledApp>
        <Router>
          <Route
            exact
            path="/"
            render={history => (
              <>
                <h1>Select a country</h1>
                <Link to="/about" className="btn">
                  <Button>About</Button>
                </Link>
                <Map
                  setCurrentCountry={setCurrentCountry}
                  history={history}
                  onMouseMove={setMousePositionHandler}
                />
                {showHoverLabel && currentCountry !== '' && (
                  <HoveringLabel
                    name={currentCountry[1]}
                    mouseX={mousePosition[0]}
                    mouseY={mousePosition[1]}
                  />
                )}
              </>
            )}
          />
          <Route exact path="/country/:name" component={Country} />
          <Route exact path="/about" component={About} />
        </Router>
      </StyledApp>
    </ThemeProvider>
  );
};

export default App;
